
























































import Vue from "vue";
import { validationMixin } from "vuelidate";
import { required, maxLength } from "vuelidate/lib/validators";
import { UserEvent } from "../types";
import { scenes } from "../config/App";
import UserEventMgr from "../utils/UserEventMgr";

export default Vue.extend({
  mixins: [validationMixin],
  validations: {
    name: { required, maxLength: maxLength(10) },
    select: { required },
    date: {
      required
    }
  },
  data() {
    return {
      name: "",
      select: null,
      categories: scenes.map(item => item.label),
      date: new Date().toISOString().substr(0, 10),
      modal: false
    };
  },

  computed: {
    nameErrors() {
      const errors: any[] = [];
      if (!this.$v.name.$dirty) return errors;
      !this.$v.name.maxLength &&
        errors.push("Name must be at most 10 characters long");
      !this.$v.name.required && errors.push("Name is required.");
      return errors;
    },
    selectErrors() {
      const errors: any[] = [];
      if (!this.$v.select.$dirty) return errors;
      !this.$v.select.required && errors.push("Item is required");
      return errors;
    }
  },

  methods: {
    submit() {
      this.$v.$touch();
      //   console.log(this.name, this.date, this.select);
      if (this.name && this.date && this.select) {
        const category = scenes.find(item => item.label === this.select);
        const userEvent: UserEvent = {
          id: "1",
          entity: this.name,
          start: this.date,
          scene: category ? category.id : 0,
          image: category ? category.image : "",
          count: 0,
          status: "",
          timeFormat: ""
        };
        // console.log("userEvent", userEvent);
        // return;
        try {
          const ueMgr = new UserEventMgr();
          ueMgr.addNew(userEvent);
        } catch (e) {
          console.log("e", e);
        }
        this.$router.go(-1);
      }
    },
    clear() {
      this.$v.$reset();
      this.name = "";
      this.date = new Date().toISOString().substr(0, 10);
      this.select = null;
      this.$router.go(-1);
    }
  }
});
